@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
.Footer{display:flex;justify-content:center;align-items:center;width:100%;height:76px;background-color:#fafafa;box-shadow:0px 1px 0px rgba(17,17,17,0.1)}.Footer__rights{font-size:13px;line-height:16px}

.Header{display:flex;align-items:center;width:100%;height:76px;padding:0 calc(9200% / 1366);box-shadow:0px 1px 0px rgba(17,17,17,0.1)}.Header__nav{flex-grow:1;padding-left:0px;text-align:center}.Header__nav-link{margin-right:25px;text-decoration:none;font-size:14px;line-height:24px;color:rgba(17,17,17,0.48)}.Header__nav-link:last-child{margin:0}

.Form-control{margin:0 0 10px;width:100%;padding:6px 16px;border:1px solid #f1f1f1;border-radius:4px}.Form-control:last-child{margin:0}.Form-control__label{margin:0 0 2px;font-size:13px;line-height:16px;color:rgba(17,17,17,0.48)}.Form-control__input{width:100%;padding:0;border:none;font-size:14px;height:24px;color:#111}

.Form-field{width:100%}.Form-field_horizontal{display:flex;margin-bottom:10px}.Form-field_horizontal:last-child{margin-bottom:0px}.Form-field_horizontal .Form-control{margin-bottom:0;margin-right:18px}.Form-field_horizontal .Form-control:last-child{margin:0}.Form-field__button{align-self:center;padding:0;border:none;background-color:transparent;font-family:inherit;font-size:14px;line-height:24px;color:#01a7fd;cursor:pointer}

.Subtitle{margin:0 0 20px;font-size:16px;font-weight:500;line-height:1.5;color:#111}

.Button{display:flex;align-items:center;padding:8px 18px;border:2px solid #01a7fd;border-radius:100px;background-color:transparent;font-family:inherit;font-size:14px;line-height:24px;color:#01a7fd;cursor:pointer}.Button__icon{margin-right:8px}.Button .Button__icon{fill:white}.Button_filled{background-color:#01a7fd;color:white}

.Personal-data-form{max-width:616px;width:100%;margin:0 auto}.Personal-data-form__fieldsets{margin-bottom:30px}.Personal-data-form__fieldset{width:100%;margin-bottom:33px}.Personal-data-form__fieldset:last-child{margin-bottom:0}.Personal-data-form__fieldset-header{display:flex;justify-content:space-between;align-items:center;height:44px;margin-bottom:11px}.Personal-data-form__fieldset-header:last-child{margin:0}.Personal-data-form__fieldset-header .Subtitle{margin:0}

.Personal-data-preview{max-width:616px;width:100%;margin:0 auto}.Personal-data-preview__block{margin-bottom:60px}.Personal-data-preview__block:last-child{margin:0}.Personal-data-preview__text{margin:0 0 20px;font-size:16px;font-weight:700;line-height:24px}.Personal-data-preview__text:last-child{margin:0}.Personal-data-preview__text_button-like{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:10px 20px;background-color:#f1f1f1;border-radius:5px}

.App{display:flex;flex-direction:column;flex-grow:1;height:100%}.main{flex-grow:1;width:100%;padding:30px 20px}.visually-hidden{position:absolute;width:1px;height:1px;margin:-1px;padding:0;overflow:hidden;border:0;white-space:nowrap;clip:rect(0 0 0 0);-webkit-clip-path:inset(100%);clip-path:inset(100%)}.hidden{display:none}

html{display:flex;flex-direction:column;min-height:100%;overflow-y:scroll}*{box-sizing:border-box;outline:none}body{display:contents;margin:0;font-family:Montserrat, Arial, Helvetica, sans-serif}#root{display:contents}

